@import '../../styles/generics';

.home {
    &__intro {
        height: 100vh;
        padding: var(--outer-padding);
        padding-top: toRem(50);
        margin: auto;
        &-container {
            display: flex;
            align-items: flex-start;
            justify-content: center;
            flex-direction: column;
            height: 100%;
            z-index: 9;
            position: relative;
            &-subtitle {
                @extend .main-subheading;
            }
            &-title {
                @extend .main-heading;
            }
            &-text {
                @extend .regular-text;
            }
            .proeficiencies-resume,
            &-text {
                opacity: .8;
                margin-top: toRem(25);
            }
        }
    }



}

@media (min-width: 1100px) {
    .home {
        &__projects {
            width: 100vh;
            height: 100vw;
            transform: rotate(-90deg) translateX(-100vh);
            transform-origin: top left;
            overflow-y: scroll;
            overflow-x: hidden;
            position: absolute;
    
            &-container {
                height: 100vh;
                position: relative;
            }
    
            &-wrapper {
                display: flex;
                flex-direction: row;
                width: 300vw;
                transform: rotate(90deg) translateY(-100vh);
                transform-origin: top left;
                scrollbar-width: none;
                -ms-overflow-style: none;
                .project {
                    width: 100vw;
                }
            }
        }

    }
}

@media (max-width: 1100px) {
    .home .status-scrollbar {
        display: none;
    }
}
::-webkit-scrollbar {
    display: none;
}